import React, { useState, useEffect } from 'react';
import { FaPencilAlt, FaPlus, FaTrash, FaEdit, FaGripVertical, FaClipboard } from 'react-icons/fa';
import { convertLyrics } from '../../../convert-lyrics';
import { useDispatch, useSelector } from 'react-redux';
import { setInstrument, transposeSongUp, transposeSongDown } from '../../../store/chordsSlice';
import { deleteSong, clearSelectedSong, setGoogleDriveConnection, setUserInfo } from '../../../store/songsSlice';
import ChordChart from './ChordChart';
import LyricLineEditor from './LyricLineEditor';
import { Spin, App, Popconfirm } from 'antd';
import { CircularProgress, Box } from '@mui/material';
import {
  DndContext,
  closestCenter,
  KeyboardSensor,
  PointerSensor,
  useSensor,
  useSensors,
} from '@dnd-kit/core';
import {
  arrayMove,
  SortableContext,
  sortableKeyboardCoordinates,
  verticalListSortingStrategy,
} from '@dnd-kit/sortable';
import {
  useSortable,
} from '@dnd-kit/sortable';
import { CSS } from '@dnd-kit/utilities';

// Sortable line component for drag and drop
const SortableLyricLine = ({ 
  line, 
  index, 
  id, 
  editingLineIndex, 
  editingEnabled, 
  hoveredLineIndex,
  setHoveredLineIndex,
  handleEditLine,
  handleInsertAfter,
  handleDeleteLine,
  handleSaveLine,
  handleCancelEdit,
  renderLyricLine,
  isThisLinePending = false,
  isDragDisabled = false,
  isPendingDelete = false,
  isAddingLine = false,
  isPendingSave = false
}) => {
  const {
    attributes,
    listeners,
    setNodeRef,
    transform,
    transition,
    isDragging,
  } = useSortable({ 
    id,
    disabled: isDragDisabled
  });

  const style = {
    transform: CSS.Transform.toString(transform),
    transition,
    opacity: isDragging ? 0.5 : (isPendingDelete ? 0.6 : 1),
    position: 'relative',
    backgroundColor: isPendingDelete ? '#f5f5f5' : 'transparent',
    color: isPendingDelete ? '#999' : 'inherit',
    pointerEvents: isPendingDelete ? 'none' : 'auto'
  };

  return (
    <div 
      ref={setNodeRef} 
      style={style} 
      className="lyric-line"
      onMouseEnter={() => setHoveredLineIndex(index)}
      onMouseLeave={() => setHoveredLineIndex(null)}
    >
      {editingLineIndex === index && !isAddingLine && editingEnabled ? (
        <LyricLineEditor
          line={line}
          onSave={(newLine) => handleSaveLine(newLine, index)}
          onCancel={handleCancelEdit}
        />
      ) : (
        <div style={{ display: 'flex', alignItems: 'center', gap: '8px' }}>
          <div className="lyric-content" style={{ flex: 1 }}>
            {renderLyricLine(line)}
          </div>
          {isPendingSave && (
            <div style={{
              display: 'flex',
              alignItems: 'center',
              gap: '4px',
              padding: '4px 8px',
              backgroundColor: '#e6f7ff',
              border: '1px solid #91d5ff',
              borderRadius: '4px',
              color: '#1890ff'
            }}>
              <Spin size="small" />
              <span style={{ fontSize: '12px' }}>Saving...</span>
            </div>
          )}
          {isPendingDelete && (
            <div style={{
              display: 'flex',
              alignItems: 'center',
              gap: '4px',
              padding: '4px 8px',
              backgroundColor: '#fff2f0',
              border: '1px solid #ffccc7',
              borderRadius: '4px',
              color: '#cf1322'
            }}>
              <Spin size="small" />
              <span style={{ fontSize: '12px' }}>Deleting...</span>
            </div>
          )}
          {hoveredLineIndex === index && editingEnabled && !isPendingDelete && (
            <div className="lyric-controls" style={{ display: 'flex', alignItems: 'center', gap: '4px' }}>
              <button 
                className="control-button edit" 
                onClick={(e) => {
                  e.stopPropagation();
                  handleEditLine(index);
                }}
                style={{
                  padding: '4px 6px',
                  border: '1px solid #ccc',
                  backgroundColor: '#f9f9f9',
                  borderRadius: '3px',
                  cursor: 'pointer',
                  fontSize: '12px'
                }}
                title="Edit this line"
              >
                <FaPencilAlt />
              </button>
              <button 
                className="control-button insert" 
                onClick={(e) => {
                  e.stopPropagation();
                  handleInsertAfter(index);
                }}
                style={{
                  padding: '4px 6px',
                  border: '1px solid #ccc',
                  backgroundColor: '#f9f9f9',
                  borderRadius: '3px',
                  cursor: 'pointer',
                  fontSize: '12px',
                  color: '#5cb85c'
                }}
                title="Insert new line after this line"
              >
                <FaPlus />
              </button>
              <button 
                className="control-button delete" 
                onClick={(e) => {
                  e.stopPropagation();
                  handleDeleteLine(index);
                }}
                style={{
                  padding: '4px 6px',
                  border: '1px solid #ccc',
                  backgroundColor: '#f9f9f9',
                  borderRadius: '3px',
                  cursor: 'pointer',
                  fontSize: '12px',
                  color: '#d9534f'
                }}
                title="Delete this line"
              >
                <FaTrash />
              </button>
              <div 
                {...(isDragDisabled ? {} : attributes)}
                {...(isDragDisabled ? {} : listeners)}
                className="drag-handle"
                style={{
                  padding: '4px 6px',
                  border: '1px solid #ddd',
                  backgroundColor: isDragDisabled ? '#e9e9e9' : '#f0f0f0',
                  borderRadius: '3px',
                  cursor: isDragDisabled ? 'not-allowed' : 'grab',
                  fontSize: '12px',
                  color: isDragDisabled ? '#999' : '#666',
                  display: 'flex',
                  alignItems: 'center',
                  transition: 'all 0.2s ease',
                  boxShadow: isDragDisabled ? 'none' : '0 1px 2px rgba(0,0,0,0.1)',
                  opacity: isDragDisabled ? 0.6 : 1
                }}
                onMouseDown={isDragDisabled ? undefined : (e) => e.currentTarget.style.cursor = 'grabbing'}
                onMouseUp={isDragDisabled ? undefined : (e) => e.currentTarget.style.cursor = 'grab'}
                title={isDragDisabled ? "Drag disabled during update" : "Drag to reorder"}
              >
                {isThisLinePending ? (
                  <Spin size="small" />
                ) : (
                  <FaGripVertical />
                )}
              </div>
            </div>
          )}
        </div>
      )}
    </div>
  );
};

const SongDetail = ({ song, onPinChord, onUpdateSong, artist, editingEnabled = true }) => {
  const { message, modal } = App.useApp();
  const [editingLineIndex, setEditingLineIndex] = useState(null);
  const [isAddingLine, setIsAddingLine] = useState(false);
  const [insertAfterIndex, setInsertAfterIndex] = useState(null); // Track where we're inserting
  const [hoveredLineIndex, setHoveredLineIndex] = useState(null);
  const [localTranspose, setLocalTranspose] = useState(0);
  const [dirty, setDirty] = useState(false);
  const [isEditingWholeSong, setIsEditingWholeSong] = useState(false);
  const [wholeSongText, setWholeSongText] = useState('');
  const [pendingSaves, setPendingSaves] = useState(new Set());
  const [pendingDragOperation, setPendingDragOperation] = useState(null);
  const [optimisticLyrics, setOptimisticLyrics] = useState(null);
  const [pendingLineIndex, setPendingLineIndex] = useState(null);
  const [pendingDeleteLines, setPendingDeleteLines] = useState(new Set());
  const [isPendingAdd, setIsPendingAdd] = useState(false);
  const [isPendingAnyOperation, setIsPendingAnyOperation] = useState(false);
  const [isSavingTranspose, setIsSavingTranspose] = useState(false);
  const [isSavingWholeSong, setIsSavingWholeSong] = useState(false);
  const [isDeletingSong, setIsDeletingSong] = useState(false);
  const [deleteCountdown, setDeleteCountdown] = useState(0);
  const dispatch = useDispatch();
  const instrument = useSelector((state) => state.chords.currentInstrument);
  const transpose = useSelector((state) => state.chords.transposeBy?.[song.title] || 0);
  const chordFingerings = useSelector((state) => state.chords.chordFingerings);
  const isGoogleDriveConnected = useSelector((state) => state.songs.isGoogleDriveConnected);

  // Set up sensors for drag and drop
  const sensors = useSensors(
    useSensor(PointerSensor),
    useSensor(KeyboardSensor, {
      coordinateGetter: sortableKeyboardCoordinates,
    })
  );

  // Extract unique chords from lyrics
  const extractChords = (lyrics) => {
    const chordRegex = /\[(.*?)\]/g;
    const allChords = [];
    
    lyrics?.forEach(line => {
      let match;
      while ((match = chordRegex.exec(line)) !== null) {
        if (!allChords.includes(match[1])) {
          allChords.push(match[1]);
        }
      }
    });
    
    return allChords;
  };

  // Helper to shift a chord name by a number of semitones
  const CHROMATIC = ['C', 'C#', 'D', 'D#', 'E', 'F', 'F#', 'G', 'G#', 'A', 'A#', 'B'];
  const FLAT_EQUIV = { 'Db': 'C#', 'Eb': 'D#', 'Gb': 'F#', 'Ab': 'G#', 'Bb': 'A#' };
  function normalizeChordName(name) {
    // Convert flats to sharps for lookup
    let base = name;
    let suffix = '';
    if (name.length > 1 && (name[1] === '#' || name[1] === 'b')) {
      base = name.slice(0, 2);
      suffix = name.slice(2);
    } else {
      base = name[0];
      suffix = name.slice(1);
    }
    if (FLAT_EQUIV[base]) base = FLAT_EQUIV[base];
    return base + suffix;
  }
  function transposeChord(chord, semitones) {
    // Handle slashed chords (e.g. A/C# -> A#/D)
    if (chord.includes('/')) {
      const [rootPart, bassPart] = chord.split('/');
      const transposedRoot = transposeChord(rootPart, semitones);
      const transposedBass = transposeChord(bassPart, semitones);
      return `${transposedRoot}/${transposedBass}`;
    }
    
    // Extract root and suffix (e.g. C#m7 -> C#, m7)
    const match = chord.match(/^([A-G][b#]?)(.*)$/);
    if (!match) return chord;
    let [_, root, suffix] = match;
    // Normalize flats to sharps
    if (FLAT_EQUIV[root]) root = FLAT_EQUIV[root];
    let idx = CHROMATIC.indexOf(root);
    if (idx === -1) return chord;
    let newIdx = (idx + semitones + 12) % 12;
    return CHROMATIC[newIdx] + suffix;
  }

  // Ensure lyrics is always an array
  const lyricsArray = Array.isArray(song.lyrics) ? song.lyrics : 
    (typeof song.lyrics === 'string' ? (song.lyrics ? song.lyrics.split('\n') : []) : []);

  const chords = (song.chords || extractChords(lyricsArray)).map(chord =>
    localTranspose !== 0 ? transposeChord(chord, localTranspose) : chord
  );

  const handleEditLine = (index) => {
    setEditingLineIndex(index);
    setIsAddingLine(false);
  };

  const handleAddLine = () => {
    // Block if there are pending operations
    if (isPendingAnyOperation || pendingDeleteLines.size > 0) {
      message.warning('Please wait for current operation to complete before adding a new line.');
      return;
    }
    
    setIsAddingLine(true);
    setEditingLineIndex(null);
  };

  const handleInsertAfter = (afterIndex) => {
    // Block if there are pending operations
    if (isPendingAnyOperation || pendingDeleteLines.size > 0) {
      message.warning('Please wait for current operation to complete before inserting a new line.');
      return;
    }
    
    setIsAddingLine(true);
    setInsertAfterIndex(afterIndex); // Track the original position
    // Set to the position where we want to insert (after the specified index)
    if (afterIndex === -1) {
      // Empty song case - insert at position 0
      setEditingLineIndex(0);
    } else {
      // Insert after the specified line
      setEditingLineIndex(afterIndex + 1);
    }
  };

  const handleSaveLine = async (newLine, index) => {
    const updatedLyrics = [...lyricsArray];
    
    if (isAddingLine) {
      // Set pending add state
      setIsPendingAdd(true);
      setIsPendingAnyOperation(true);
      
      // Check if we're inserting at a specific position or adding at the end
      const targetIndex = editingLineIndex !== null && editingLineIndex <= lyricsArray.length
        ? editingLineIndex // Insert at specific position
        : updatedLyrics.length; // Add at the end
      
      // Insert at target position
      updatedLyrics.splice(targetIndex, 0, newLine);
      
      setOptimisticLyrics(updatedLyrics);
      setPendingSaves(prev => new Set([...prev, targetIndex]));
      
      // Exit edit mode immediately for better UX
      setEditingLineIndex(null);
      setIsAddingLine(false);
      
      // Save the add operation
      onUpdateSong({
        ...song,
        lyrics: updatedLyrics
      }).then(() => {
        message.success('Line added successfully!');
        setOptimisticLyrics(null);
        setIsPendingAdd(false);
        setIsPendingAnyOperation(false);
        setPendingSaves(prev => {
          const newSet = new Set(prev);
          newSet.delete(targetIndex);
          return newSet;
        });
        // Ensure we completely reset editing state
        setEditingLineIndex(null);
        setIsAddingLine(false);
        setInsertAfterIndex(null);
      }).catch((error) => {
        console.error('Failed to add line:', error);
        message.error('Failed to add new line. Please try again.');
        // Revert optimistic update
        setOptimisticLyrics(null);
        setIsPendingAdd(false);
        setIsPendingAnyOperation(false);
        setPendingSaves(prev => {
          const newSet = new Set(prev);
          newSet.delete(targetIndex);
          return newSet;
        });
        setIsAddingLine(true);
        setEditingLineIndex(null);
        setInsertAfterIndex(null);
      });
    } else {
      // Set pending edit state
      setIsPendingAnyOperation(true);
      setPendingSaves(prev => new Set([...prev, index]));
      
      // Update existing line - optimistic update
      updatedLyrics[index] = newLine;
      setOptimisticLyrics(updatedLyrics);
      
      // Exit edit mode immediately for better UX
      setEditingLineIndex(null);
      
      // Save the edit operation
      onUpdateSong({
        ...song,
        lyrics: updatedLyrics
      }).then(() => {
        message.success('Line updated successfully!');
        setOptimisticLyrics(null);
        setIsPendingAnyOperation(false);
        setPendingSaves(prev => {
          const newSet = new Set(prev);
          newSet.delete(index);
          return newSet;
        });
      }).catch((error) => {
        console.error('Failed to update line:', error);
        message.error('Failed to update line. Please try again.');
        // Revert optimistic update
        setOptimisticLyrics(null);
        setIsPendingAnyOperation(false);
        setPendingSaves(prev => {
          const newSet = new Set(prev);
          newSet.delete(index);
          return newSet;
        });
        setEditingLineIndex(index);
      });
    }
  };

  const handleCancelEdit = () => {
    setEditingLineIndex(null);
    setIsAddingLine(false);
    setInsertAfterIndex(null);
  };

  // Handle deleting a line with optimistic updates
  const handleDeleteLine = async (index) => {
    // Block if there are pending operations
    if (isPendingAnyOperation || pendingDeleteLines.has(index)) {
      message.warning('Please wait for current operation to complete before deleting this line.');
      return;
    }

    // Set pending delete state
    setIsPendingAnyOperation(true);
    setPendingDeleteLines(prev => new Set([...prev, index]));

    const updatedLyrics = [...lyricsArray];
    updatedLyrics.splice(index, 1);
    
    // Optimistic update
    setOptimisticLyrics(updatedLyrics);

    try {
      await onUpdateSong({
        ...song,
        lyrics: updatedLyrics
      });
      
      message.success('Line deleted successfully!');
      setOptimisticLyrics(null);
      setIsPendingAnyOperation(false);
      setPendingDeleteLines(prev => {
        const newSet = new Set(prev);
        newSet.delete(index);
        return newSet;
      });
    } catch (error) {
      console.error('Failed to delete line:', error);
      message.error('Failed to delete line. Please try again.');
      
      // Revert optimistic update
      setOptimisticLyrics(null);
      setIsPendingAnyOperation(false);
      setPendingDeleteLines(prev => {
        const newSet = new Set(prev);
        newSet.delete(index);
        return newSet;
      });
    }
  };

  // Handle drag end for reordering lines with optimistic updates
  const handleDragEnd = async (event) => {
    const { active, over } = event;

    if (active.id !== over.id) {
      // Set pending reorder state
      setIsPendingAnyOperation(true);
      
      const oldIndex = lyricsArray.findIndex((_, index) => index.toString() === active.id);
      const newIndex = lyricsArray.findIndex((_, index) => index.toString() === over.id);
      
      // Optimistic update - immediately show the new order
      const newLyrics = arrayMove(lyricsArray, oldIndex, newIndex);
      setOptimisticLyrics(newLyrics);
      setPendingLineIndex(newIndex); // Track which line is pending
      
      // Save the reorder operation
      onUpdateSong({
        ...song,
        lyrics: newLyrics
      }).then(() => {
        message.success('Lines reordered successfully!');
        setOptimisticLyrics(null);
        setIsPendingAnyOperation(false);
        setPendingLineIndex(null);
      }).catch((error) => {
        console.error('Failed to reorder lines:', error);
        message.error('Failed to reorder lines. Please try again.');
        // Revert optimistic update
        setOptimisticLyrics(null);
        setIsPendingAnyOperation(false);
        setPendingLineIndex(null);
      });
    }
  };

  // Remove the old handleMoveLine function as it's replaced by drag and drop

  // Handle whole song editing
  const handleEditWholeSong = () => {
    setWholeSongText(lyricsArray.join('\n'));
    setIsEditingWholeSong(true);
  };

  const handleSaveWholeSong = async () => {
    setIsSavingWholeSong(true);
    try {
      const newLyrics = wholeSongText.split('\n').filter(line => line.trim() !== '');
      await onUpdateSong({
        ...song,
        lyrics: newLyrics,
        chords: extractChords(newLyrics)
      });
      setIsEditingWholeSong(false);
      message.success('Song lyrics saved successfully!');
    } catch (error) {
      console.error('Failed to save whole song:', error);
      message.error('Failed to save song. Please try again.');
    } finally {
      setIsSavingWholeSong(false);
    }
  };

  // Helper function to check if error is authentication-related
  const isAuthError = (error) => {
    if (!error) return false;
    const message = error.message || error || '';
    const authErrorPatterns = [
      'User not signed in to Google Drive',
      'Expected OAuth 2 access token',
      'login cookie or other valid authentication credential', 
      'Invalid Credentials',
      'Authentication failed',
      'unauthorized_client',
      'invalid_token',
      'expired_token',
      'access_denied',
      'token_expired',
      'Request had invalid authentication credentials'
    ];
    return authErrorPatterns.some(pattern => 
      message.toLowerCase().includes(pattern.toLowerCase())
    );
  };

  // Handle opening the popconfirm - start countdown
  const handleDeleteConfirm = () => {
    // This is called when user clicks the actual Delete button in the popconfirm
    handleDeleteSong();
  };

  // Start countdown when popconfirm opens
  const handlePopconfirmOpen = (open) => {
    if (open) {
      setDeleteCountdown(3.0); // Start 3-second countdown with decimal
      const timer = setInterval(() => {
        setDeleteCountdown(prev => {
          if (prev <= 0.1) {
            clearInterval(timer);
            return 0;
          }
          return Math.max(0, prev - 0.1); // Decrease by 0.1 every 100ms
        });
      }, 100); // Update every 100ms (10 times per second)
    } else {
      // Reset countdown when popconfirm closes
      setDeleteCountdown(0);
    }
  };

  // Handle actual song deletion
  const handleDeleteSong = async () => {
    setIsDeletingSong(true);
    try {
      await dispatch(deleteSong({
        artistName: artist.name,
        albumTitle: song.album?.title,
        songTitle: song.title,
        isGoogleDriveConnected
      })).unwrap();

      message.success('Song deleted successfully!');
      
      // Clear selected song to navigate away
      dispatch(clearSelectedSong());
      
    } catch (error) {
      console.error('Failed to delete song:', error);
      
      // Check if this is an authentication error
      if (isAuthError(error)) {
        dispatch(setGoogleDriveConnection(false));
        dispatch(setUserInfo(null));
        message.error('Your Google Drive session has expired. Please sign in again to delete songs.');
      } else {
        message.error(error.message || 'Failed to delete song. Please try again.');
      }
    } finally {
      setIsDeletingSong(false);
      setDeleteCountdown(0);
    }
  };

  const handleCancelWholeSong = () => {
    setIsEditingWholeSong(false);
    setWholeSongText('');
  };

  const renderDeletePopconfirmTitle = () => (
    <div>
      <div>Delete "{song.title}"?</div>
      <div style={{ fontSize: '12px', color: '#666', marginTop: '4px' }}>
        This action cannot be undone.
      </div>
    </div>
  );

  const renderDeletePopconfirmContent = () => (
    <div style={{ minWidth: '200px' }}>
      <div>
        <strong>Artist:</strong> {artist.name}<br />
        <strong>Album:</strong> {song.album?.title}<br />
        <strong>Song:</strong> {song.title}
      </div>
    </div>
  );

  // Function to render a lyric line with chord formatting above text
  const renderLyricLine = (line) => {
    const chordRegex = /\[(.*?)\]/g;
    const chordPositions = [];
    let plainText = line;
    let match;
    
    // Extract chord positions and create plain text
    while ((match = chordRegex.exec(line)) !== null) {
      chordPositions.push({
        chord: match[1],
        position: match.index,
        length: match[0].length
      });
    }
    
    // Remove chord markers for plain text
    plainText = plainText.replace(/\[(.*?)\]/g, '');
    
    // Calculate positions for chord labels
    const adjustedChordPositions = chordPositions.map((item, index) => {
      let adjustment = 0;
      for (let i = 0; i < index; i++) {
        adjustment += chordPositions[i].length;
      }
      // Transpose the chord if needed
      const transposedChord = localTranspose !== 0 ? transposeChord(item.chord, localTranspose) : item.chord;
      return {
        ...item,
        chord: transposedChord,
        position: item.position - adjustment
      };
    });
    
    return (
      <div className="lyric-line-with-chords">
        <div className="chord-labels">
          {adjustedChordPositions.map((item, idx) => (
            <span 
              key={`chord-${idx}`} 
              className="chord-label"
              style={{ left: `${item.position}ch` }}
              onClick={() => onPinChord(item.chord)}
            >
              {item.chord}
            </span>
          ))}
        </div>
        <div className="lyric-text-only">
          {plainText}
        </div>
      </div>
    );
  };

  React.useEffect(() => {
    // Sync local transpose with redux only when song changes
    setLocalTranspose(transpose);
    setDirty(false);
  }, [song.title]);

  const handleTransposeUp = () => {
    setLocalTranspose((prev) => prev + 1);
    setDirty(true);
    dispatch(transposeSongUp(song.title));
  };
  const handleTransposeDown = () => {
    setLocalTranspose((prev) => prev - 1);
    setDirty(true);
    dispatch(transposeSongDown(song.title));
  };
  const handleSaveTranspose = async () => {
    setIsSavingTranspose(true);
    try {
      // Apply transposition to all lyrics inline
      const transposedLyrics = lyricsArray.map(line => {
        const chordRegex = /\[([^\]]+)\]/g;
        return line.replace(chordRegex, (match, chord) => {
          const transposedChord = transposeChord(chord, localTranspose);
          return `[${transposedChord}]`;
        });
      });

      const updatedSong = { 
        ...song, 
        lyrics: transposedLyrics,
        chords: extractChords(transposedLyrics),
        chordFingerings: chordFingerings
      };
      
      await onUpdateSong(updatedSong);
      
      // Reset local transpose since it's now baked into the lyrics
      setLocalTranspose(0);
      setDirty(false);
      
      // Show success message
      message.success('Transposed lyrics saved successfully!');
    } catch (error) {
      console.error('Failed to save transposed lyrics:', error);
      message.error('Failed to save transposed lyrics. Please try again.');
    } finally {
      setIsSavingTranspose(false);
    }
  };

  return (
    <div className="song-detail">      
      {/* Chord section */}
      <div className="chords-section">
        <div className="chords-header" style={{ display: 'flex', alignItems: 'center', justifyContent: 'space-between', gap: '1rem' }}>
          <div style={{ display: 'flex', alignItems: 'flex-end', gap: '1rem' }}>
            <h3 style={{ margin: 0 }}>{song.title}</h3>
            <i style={{ margin: 0 }}>{artist.name}</i>
          </div>
          <div style={{ display: 'flex', alignItems: 'center', gap: '1rem', flex: 1, justifyContent: 'center' }}>
            <button
              className="transpose-btn"
              title="Transpose Down"
              onClick={handleTransposeDown}
              style={{ fontSize: '1.2em', padding: '0.2em 0.6em' }}
            >
              -
            </button>
            <span style={{ minWidth: 40, textAlign: 'center' }}>Transpose: {localTranspose > 0 ? `+${localTranspose}` : localTranspose} semitones</span>
            <button
              className="transpose-btn"
              title="Transpose Up"
              onClick={handleTransposeUp}
              style={{ fontSize: '1.2em', padding: '0.2em 0.6em' }}
            >
              +
            </button>
            <button
              className="save-transpose-btn"
              onClick={handleSaveTranspose}
              disabled={!dirty || !editingEnabled || isSavingTranspose}
              style={{ 
                marginLeft: '1em', 
                padding: '0.2em 0.8em', 
                opacity: dirty && editingEnabled && !isSavingTranspose ? 1 : 0.5,
                display: 'flex',
                alignItems: 'center',
                gap: '0.5em'
              }}
            >
              {isSavingTranspose && <Spin size="small" />}
              Save Transposed Lyrics
            </button>
          </div>
          <div className="instrument-selector" style={{ display: 'flex', alignItems: 'center', gap: '0.5rem', flexWrap: 'wrap' }}>
            <label htmlFor="instrument-select" style={{ marginRight: 0 }}>Instrument:</label>
            <select
              id="instrument-select"
              value={instrument}
              onChange={(e) => dispatch(setInstrument(e.target.value))}
              style={{ minWidth: 120 }}
            >
              <option value="ukulele">Ukulele</option>
              <option value="guitar">Guitar</option>
              <option value="piano">Piano</option>
              <option value="bassGuitar">Bass Guitar</option>
              <option value="bassUkulele">Bass Ukulele</option>
              <option value="baritoneUkulele">Baritone Ukulele</option>
            </select>
          </div>
        </div>
        <div className="chord-container">
          {chords.map(chord => (
            <div key={chord} className="chord-item" onClick={() => onPinChord(chord)}>
              <ChordChart
                chord={chord}
                instrument={instrument}
              />
            </div>
          ))}
        </div>
      </div>
      
      {/* Lyrics section */}
      <div className="lyrics-section">
        <div className="lyrics-container">
          <div style={{ display: 'flex', alignItems: 'flex-end', gap: '1rem', justifyContent: 'space-between' }}>
            <div style={{ display: 'flex', alignItems: 'flex-end', gap: '1rem' }}>
              <h3 style={{ margin: 0 }}>{song.title}</h3>
              <i style={{ margin: 0 }}>{artist.name}</i>
            </div>
            {editingEnabled && !isEditingWholeSong && (
              <div style={{ display: 'flex', gap: '0.5rem' }}>
                <button 
                  className="edit-whole-song-btn"
                  onClick={handleEditWholeSong}
                  style={{ 
                    padding: '0.5em 1em', 
                    fontSize: '0.9em',
                    backgroundColor: '#4285f4',
                    color: 'white',
                    border: '1px solid #3367d6',
                    borderRadius: '4px',
                    cursor: 'pointer',
                    display: 'flex',
                    alignItems: 'center',
                    gap: '0.5em',
                    transition: 'background-color 0.2s ease'
                  }}
                  onMouseEnter={(e) => e.target.style.backgroundColor = '#3367d6'}
                  onMouseLeave={(e) => e.target.style.backgroundColor = '#4285f4'}
                >
                  <FaEdit /> Edit Whole Song
                </button>
                <Popconfirm
                  title={renderDeletePopconfirmTitle()}
                  description={renderDeletePopconfirmContent()}
                  onConfirm={handleDeleteConfirm}
                  onOpenChange={handlePopconfirmOpen}
                  okText={
                    deleteCountdown > 0 ? (
                      <div style={{ display: 'flex', alignItems: 'center', gap: '8px', position: 'relative' }}>
                        <Box style={{ position: 'relative', display: 'inline-flex', alignItems: 'center' }}>
                          <CircularProgress
                            variant="determinate"
                            value={((3.0 - deleteCountdown) / 3.0) * 100}
                            size={20}
                            thickness={8}
                            style={{ color: '#dc3545' }}
                          />
                          <Box
                            style={{
                              position: 'absolute',
                              top: '50%',
                              left: '50%',
                              transform: 'translate(-50%, -50%)',
                              color: '#dc3545',
                              fontWeight: 'bold',
                              fontSize: '10px'
                            }}
                          >
                            {(deleteCountdown+1).toFixed()}
                          </Box>
                        </Box>
                        Please wait...
                      </div>
                    ) : isDeletingSong ? (
                      <div style={{ display: 'flex', alignItems: 'center', gap: '8px' }}>
                        <Spin size="small" />
                        Deleting...
                      </div>
                    ) : (
                      "Delete"
                    )
                  }
                  cancelText={isDeletingSong ? null : "Cancel"}
                  okType="danger"
                  showCancel={!isDeletingSong}
                  okButtonProps={{
                    disabled: deleteCountdown > 0 || isDeletingSong
                  }}
                  placement="bottomRight"
                >
                  <button 
                    className="delete-song-btn"
                    style={{ 
                      padding: '0.5em 1em', 
                      fontSize: '0.9em',
                      backgroundColor: '#dc3545',
                      color: 'white',
                      border: '1px solid #c82333',
                      borderRadius: '4px',
                      cursor: 'pointer',
                      display: 'flex',
                      alignItems: 'center',
                      gap: '0.5em',
                      transition: 'background-color 0.2s ease'
                    }}
                    onMouseEnter={(e) => e.target.style.backgroundColor = '#c82333'}
                    onMouseLeave={(e) => e.target.style.backgroundColor = '#dc3545'}
                  >
                    <FaTrash /> Delete Song
                  </button>
                </Popconfirm>
              </div>
            )}
            {isEditingWholeSong && (
              <button 
                onClick={() => {
                  try {
                    const converted = convertLyrics(wholeSongText);
                    setWholeSongText(converted);
                    message.success('Lyrics converted successfully!');
                  } catch (error) {
                    console.error('Error converting lyrics:', error);
                    message.error('Failed to convert lyrics format. Please check your input.');
                  }
                }}
                style={{ 
                  padding: '0.5em 1em', 
                  fontSize: '0.9em',
                  backgroundColor: '#5C6BC0',
                  color: 'white',
                  border: '1px solid #3F51B5',
                  borderRadius: '4px',
                  cursor: 'pointer',
                  display: 'flex',
                  alignItems: 'center',
                  gap: '0.5em',
                  marginLeft: '10px',
                  transition: 'background-color 0.2s ease'
                }}
                onMouseEnter={(e) => e.target.style.backgroundColor = '#3F51B5'}
                onMouseLeave={(e) => e.target.style.backgroundColor = '#5C6BC0'}
              >
                <FaClipboard /> Convert from Clipboard Format
              </button>
            )}
          </div>
          
          {isEditingWholeSong ? (
            <div className="whole-song-editor" style={{ marginTop: '1rem' }}>
              <textarea
                value={wholeSongText}
                onChange={(e) => setWholeSongText(e.target.value)}
                rows={15}
                style={{
                  width: '100%',
                  fontFamily: 'monospace',
                  fontSize: '14px',
                  padding: '10px',
                  border: '1px solid #ccc',
                  borderRadius: '4px'
                }}
                placeholder="Enter lyrics with [Chord] notation..."
              />
              <div style={{ marginTop: '10px', display: 'flex', gap: '10px' }}>
                <button 
                  onClick={handleSaveWholeSong}
                  disabled={isSavingWholeSong}
                  style={{ 
                    padding: '0.5em 1em',
                    backgroundColor: isSavingWholeSong ? '#ccc' : '#4CAF50',
                    color: 'white',
                    border: 'none',
                    borderRadius: '4px',
                    cursor: isSavingWholeSong ? 'not-allowed' : 'pointer',
                    display: 'flex',
                    alignItems: 'center',
                    gap: '0.5em'
                  }}
                >
                  {isSavingWholeSong && <Spin size="small" />}
                  {isSavingWholeSong ? 'Saving...' : 'Save'}
                </button>
                <button 
                  onClick={handleCancelWholeSong}
                  style={{ 
                    padding: '0.5em 1em',
                    backgroundColor: '#f44336',
                    color: 'white',
                    border: 'none',
                    borderRadius: '4px',
                    cursor: 'pointer'
                  }}
                >
                  Cancel
                </button>
              </div>
            </div>
          ) : (
            <DndContext 
              sensors={sensors}
              collisionDetection={closestCenter}
              onDragEnd={handleDragEnd}
            >
              <SortableContext 
                items={(optimisticLyrics || lyricsArray).map((_, index) => index.toString())}
                strategy={verticalListSortingStrategy}
              >
                {(optimisticLyrics || lyricsArray) && (optimisticLyrics || lyricsArray).map((line, index) => (
                  <div key={index}>
                    <SortableLyricLine
                      id={index.toString()}
                      line={line}
                      index={index}
                      editingLineIndex={editingLineIndex}
                      editingEnabled={editingEnabled}
                      hoveredLineIndex={hoveredLineIndex}
                      setHoveredLineIndex={setHoveredLineIndex}
                      handleEditLine={handleEditLine}
                      handleInsertAfter={handleInsertAfter}
                      handleDeleteLine={handleDeleteLine}
                      handleSaveLine={handleSaveLine}
                      handleCancelEdit={handleCancelEdit}
                      renderLyricLine={renderLyricLine}
                      isThisLinePending={pendingLineIndex === index}
                      isDragDisabled={pendingDragOperation !== null || pendingDeleteLines.has(index) || pendingSaves.has(index)}
                      isPendingDelete={pendingDeleteLines.has(index)}
                      isPendingSave={pendingSaves.has(index)}
                      isAddingLine={isAddingLine}
                    />
                    {/* Show line editor when inserting after this line */}
                    {isAddingLine && insertAfterIndex === index && editingEnabled && (
                      <LyricLineEditor
                        line=""
                        onSave={(newLine) => handleSaveLine(newLine, index + 1)}
                        onCancel={handleCancelEdit}
                      />
                    )}
                  </div>
                ))}
              </SortableContext>
              
              {/* Add a button to add line at the end when the song is empty */}
              {editingEnabled && lyricsArray.length === 0 && !isAddingLine && (
                <button 
                  className="add-lyric-button" 
                  onClick={() => handleInsertAfter(-1)}
                  style={{
                    padding: '10px 20px',
                    border: '2px dashed #ccc',
                    backgroundColor: 'transparent',
                    borderRadius: '8px',
                    cursor: 'pointer',
                    fontSize: '16px',
                    color: '#666',
                    display: 'flex',
                    alignItems: 'center',
                    gap: '8px',
                    margin: '20px 0',
                    width: '100%',
                    justifyContent: 'center'
                  }}
                >
                  <FaPlus /> Add First Line
                </button>
              )}
              
              {/* Add line editor for empty song case */}
              {isAddingLine && insertAfterIndex === -1 && editingEnabled && (
                <LyricLineEditor
                  line=""
                  onSave={(newLine) => handleSaveLine(newLine, 0)}
                  onCancel={handleCancelEdit}
                />
              )}
            </DndContext>
          )}
        </div>
      </div>
    </div>
  );
};

// Wrap component with App provider for message API
const SongDetailWithProvider = (props) => (
  <App>
    <SongDetail {...props} />
  </App>
);

export default SongDetailWithProvider;
